import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";

import CashBankCreateForm from "./createForm";

import notification from "../../../../components/notification";
// import { Input, Select, Divider } from "antd";

import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/cashBank/cashBank/actions";
import { Box } from "../../../App/app.style";

const redirectTo = "/dashboard/setup/cash-banks";

class CreateCashBank extends Component {
  // Get All data diawal
  componentDidMount() {
    const { createCashBank } = this.props;
    createCashBank();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.cashBank.error) {
      notification("error", this.props.cashBank.error);
      this.props.clearError();
    }

    if (this.props.cashBank.success) {
      this.props.history.replace(redirectTo);
    }
  }


  handleSubmit = value => {
    const { storeCashBank } = this.props;

    if(value.is_cash===1){
      value.bank_account_number = "";
    }
    value.start_date = value.start_date.format("YYYY-MM-DD HH:mm:ss");
    //console.log(value);
    storeCashBank(value);
  };

  render() {
    const { user } = this.props.auth;
    //let { number } = user ? user : [];
      
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader title={<IntlMessages id="cashBanks.add.title" />}
            style={{ margin:0 }}
          >
            <Button onClick={() => this.props.history.replace(redirectTo)}>
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          <CashBankCreateForm
            submit={this.handleSubmit}
            cashBank={this.props.cashBank}
            user={user}
          />
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return { 
    cashBank: state.cashBank,
    auth: state.auth
  };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(CreateCashBank);
