const actions = {
  getCashBanks: (page, search, filter) => ({
    type: 'GET_CASH_BANKS_REQUEST',
    payload: {
      page: page,
      search: search,
      filter: filter
    }
  }),
  createCashBank: () => ({
    type: 'CREATE_CASH_BANK_REQUEST'
  }),
  storeCashBank: data => ({
    type: 'STORE_CASH_BANK_REQUEST',
    payload: { body: data }
  }),
  editCashBank: id_cash_bank => ({
    type: 'EDIT_CASH_BANK_REQUEST',
    payload: { id_cash_bank: id_cash_bank }
  }),
  updateCashBank: (id_cash_bank, data) => ({
    type: 'UPDATE_CASH_BANK_REQUEST',
    payload: {
      id_cash_bank: id_cash_bank,
      body: data
    }
  }),
  showCashBank: id_cash_bank => ({
    type: 'SHOW_CASH_BANK_REQUEST',
    payload: { id_cash_bank: id_cash_bank }
  }),
  deactivateCashBank: id_cash_bank => ({
    type: 'DEACTIVATE_CASH_BANK_REQUEST',
    payload: { id_cash_bank }
  }),
  activateCashBank: id_cash_bank => ({
    type: 'ACTIVATE_CASH_BANK_REQUEST',
    payload: { id_cash_bank }
  }),
  clearSuccess: () => ({
    type: "CLEAR_SUCCESS"
  }),
  clearError: () => ({
    type: "CLEAR_ERROR"
  }),
  clearErrorField: () => ({
    type: "CLEAR_ERROR_FIELD"
  })
};

export default actions;
